$main-color:#d83734;
$secondary-color:#a19181;
$menu-color:#525252;

html{
    font-size:16px;
    font-family:Arial;
}
/* main menu */
#toggle-right {
    display: none;
    &:checked ~ .main_menu{
        right: 0%;
    }
}

.main_menu{
    position:fixed;
    top:0;
    right:-25%;
    width:25%;
    height:100%;
    background:$menu-color;
    z-index: 10;
    padding:2em 0 0 0;
    transition: all 0.15s ease-out 0s;
    .close_drawer{
        padding-right:2em;
        cursor: pointer;
        width:100%;
        text-align:right;
        margin-bottom:4em;
        img{
            width:20px;
            height:20px;
        }
    }
    nav{
        ul{
            padding:0;
            width:100%;
            a{
                color:white;
                .list_item{
                    list-style-type:none;
                    padding:1em 0 1em 0;
                    text-align:center;
                    transition: background 0.15s ease-out;
                }
                &:hover{
                    text-decoration: none;
                }
                &:hover>li{
                    background:$main-color;
                }
            }
        }
    }
    #main_menu_social{
        margin-top: 4em;
        padding:0 1em 0 1em;
        &>.row{
            &>div{
                text-align: center;
                &>img{
                    width:40px;
                    height:40px;
                }
            }
        }

    }
}
/* main menu end*/

/* general css */
.homepage_section{
    margin-bottom:2.5em;
}

.section_title{
    color:$secondary-color;
}

.right{
    text-align:right;
}

.home_social_top{

}

.home_social_bottom{

}
/* general css end*/


/* header */
.lang{
    color:$secondary-color;
}

.lang_active{
    color:$main-color;
}

#header_nav_container{
    margin-bottom:1em;
    #language{
        text-align:right;
    }
    #header_bar{
        display:flex;
        align-items: center;
        justify-content: center;
        #header_logo{
            text-align:center;
            img{
                height:92px;
            }
        }
        #header_social{

            img{
                height:20px;
            }
            a:not(:last-child){
                margin-right:2em;
            }
        }
        #header_search{
            text-align:right;
            img{
                height:20px;
            }
        }
        #header_nav{
            text-align: right;
            #drawer_trigger{
                cursor:pointer;
                span{
                    color:$secondary-color;
                }
                img{
                    height:20px;
                    margin-left:0.5em;
                }
            }
        }
    }
}
/* header end*/

/* home banner */
#homepage_main_banner_container{
    position:relative;
    img{
        width:100%;
        height:700px;
    }
    .text_in_main_banner{
        position:absolute;
        top:200px;
        width:30%;
        left:50px;
        color:black;
    }
    #who_we_are_container{
        position: absolute;
        bottom:-80px;
        left:12em;
        right:12em;
        background:#aaa;
        .container_2{
            position:relative;
            &>.row{
                margin:0;
                display:flex;
                align-items: center;
                justify-content: center;
                background:$main-color;
                #who_we_are_home_title{
                    text-align: center;
                }
                #who_we_are_home_text{
                    background:$secondary-color;
                    padding:2.5em;
                }
            }
        }
    }
}
/* home banner end*/

/* services section home*/
#services_container{
    text-align:center;
    margin-top:6em;
    p{
        padding:0 3em 0 3em;
    }
    .row{
        margin-top:2em;
        .service_homepage_img_container{
            position:relative;
            img{
                width:100%;
            }
            span{
                position:absolute;
                top:0;
                left:0;
                width:100%;
                text-align:center;
                margin-top:3em;
            }
        }
    }
}
/* services section home end*/

/* news home section*/
#news_container{
    #publications_container{
        div{
            padding:0.5em 0 0.5em 0;
            border-bottom:1px solid black;
        }
    }
    &>.row{
        margin:0;
        #news_more{
            background:#525252;
            padding:4em 2em 3em 2em;
            p{
                color:white;
                width:70%;
            }
            a{
                color:$secondary-color;
            }
        }
    }
}
/* news home section end */

/* home clients section */
#selected_clients_container{
    background:white;
    text-align:center;
    padding:5em 4em 3em 4em;
    h2{
        margin-bottom:1em;
    }
    a{
        color:$main-color;
    }
    #auditwise_clients{
        margin-bottom:2em;
        .homepage_clients{
            background:#aaa;
            height:100px;
        }
    }
}
/* home clients section end*/

/* newsletter */
#newsletter_container{
    background:grey;
    #newsletter{
        padding:4em 4em 3em 4em;
        p,h2{
            text-align:center;
        }
        p{
            color:white;
            margin-bottom:4em;
        }
        input,button{
            border-radius:0;
        }
        button{
            color:white;
            background:$secondary-color;
            border-color:$secondary-color;
        }
        .form-control:focus{
            border-color:$secondary-color;
            box-shadow:0px 0px 5px $secondary-color;
        }
    }
}
/* newsletter end */

/* footer */
#footer_container{
    color:white;
    background:$main-color;
    padding-top:6em;
    padding-bottom:2em;
    &>.container{
        &>.row:nth-child(1){
            margin-bottom:4em;
            #footer_contact{
                p{
                    margin:0;
                }
                #address{
                    margin-bottom:0.5em;
                }
            }
            #footer_logo{
                text-align:center;
                img{
                    height:127px;
                }
            }
            #footer_social{
                text-align:right;
                margin-top:5em;
                &>.row:nth-child(1){
                    img{
                        height:25px;
                    }
                }
                &>.row:nth-child(2){
                    margin-top:1em;
                    img{
                        height:20px;
                    }
                    #footer_contact_us{
                        color:white;
                    }
                }
            }
        }
        &>.row:nth-child(2){
            #copright_section{

            }
            #designed_developed_section{
                text-align:right;
            }
        }
    }
}
/* footer end*/


